// ===================== CHECKBOXES ==================== //
.check-box {
    width: 1px;
    height: 1px;
    overflow: hidden;
    position: absolute;
    clip: rect(0 0 0 0);
}

.check-style {
    position: relative;
    width: 2rem;
    height: 2rem;
    border: 0.1rem solid $gray;
    @media (max-width: 1366px) {
        width: 1.5rem;
        height: 1.5rem;
    }
}

.check-box:checked + .check-style:before {
    content: '';
    width: 1rem;
    height: 1rem;
    background-color: $accent;
    position: absolute;
    @extend %pos-center-xy;
}

.check-box--color {
    & + .check-style {
        border: none;
    }
    &:checked + .check-style:before {
        display: none;
    }
}

// ===================== RADIO ===================== //
.radio-box {
    width: 1px;
    height: 1px;
    overflow: hidden;
    position: absolute;
    clip: rect(0 0 0 0);
}

.radio-style {
    width: 100%;
    height: 5.4rem;
    border: 0.1rem solid $gray;
    @extend %flex-center;
    @extend %text-16;
    cursor: pointer;
    @media (max-width: 1366px) {
        height: 4rem;
        font-size: 1.2rem;
        line-height: 1.8rem;
    }
}

.radio-box:checked + .radio-style {
    background-color: $accent;
    color: #fff;
}

// input number
input[type='number'] {
    &::-webkit-inner-spin-button {
        -webkit-appearance: none;
    }

    &::-webkit-outer-spin-button {
        -webkit-appearance: none;
    }
}

input[type='number'] {
    -moz-appearance: textfield;
}
input[type='number']:hover,
input[type='number']:focus {
    -moz-appearance: number-input;
}

// jquery ui range slider
.ui-widget-content {
    background-color: $gray;
    border-radius: 0;
    height: 0.1rem;
}

.ui-widget.ui-widget-content {
    border: none;
}

.ui-slider-range {
    background-color: $accent;
}

.ui-state-default,
.ui-widget-content .ui-state-default {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    background-color: $accent;
    border: none;
}

.ui-slider-horizontal .ui-slider-handle {
    top: -0.45rem;
}

